<template>
  <ion-page>
    <ion-header mode="ios">
      <ion-toolbar>
        <ion-title>Raport Ujian SNBT</ion-title>
        <ion-back-button slot="start" default-href="/" />
      </ion-toolbar>
    </ion-header>

    <ion-content :fullscreen="true" class="report-content">
      <!-- Loading State -->
      <div v-if="loading" class="loading-container">
        <ion-spinner name="crescent"></ion-spinner>
        <p>Memuat raport...</p>
      </div>

      <!-- Main Report Content -->
      <div v-else-if="examResults.length > 0" class="report-container">
        <!-- User Profile Section -->
        <ion-card class="profile-card">
          <ion-card-header>
            <ion-card-title>
              <ion-avatar class="profile-avatar">
                <img :src="userPhoto || 'https://ionicframework.com/docs/img/demos/avatar.svg'" alt="User Photo">
              </ion-avatar>
              {{ userFullName }}
            </ion-card-title>
            <ion-card-subtitle>Raport Try Out SNBT</ion-card-subtitle>
          </ion-card-header>
        </ion-card>

        <!-- Overall Performance -->
        <ion-card class="summary-card">
          <ion-card-header>
            <ion-card-title>Ringkasan Performa</ion-card-title>
          </ion-card-header>
          <ion-card-content>
            <div class="performance-grid">
              <div class="performance-item">
                <div class="performance-value">{{ averageScore.toFixed(2) }}</div>
                <div class="performance-label">Rata-Rata</div>
              </div>
              <div class="performance-item">
                <div class="performance-value">{{ highestScore }}</div>
                <div class="performance-label">Skor Tertinggi</div>
              </div>
              <div class="performance-item">
                <div class="performance-value">{{ examResults.length }}</div>
                <div class="performance-label">Total Ujian</div>
              </div>
            </div>
          </ion-card-content>
        </ion-card>

        <!-- Exam History -->
        <ion-card class="history-card">
          <ion-card-header>
            <ion-card-title>Riwayat Ujian</ion-card-title>
          </ion-card-header>
          <ion-card-content>
            <ion-list lines="none">
              <ion-item 
                v-for="(result, index) in examResults" 
                :key="index"
                @click="viewExamDetail(result.jadwalId)"
                class="exam-item"
              >
                <ion-label>
                  <h3>Ujian #{{ examResults.length - index }}</h3>
                  <p>{{ formatDate(result.createdAt) }}</p>
                </ion-label>
                <ion-badge slot="end" :color="getScoreColor(result.score)">
                  {{ result.score.toFixed(2) }}
                </ion-badge>
              </ion-item>
            </ion-list>
          </ion-card-content>
        </ion-card>

        <!-- Skill Breakdown -->
        <ion-card class="skills-card">
          <ion-card-header>
            <ion-card-title>Analisis Kemampuan</ion-card-title>
          </ion-card-header>
          <ion-card-content>
            <div class="skill-chart">
              <div 
                v-for="(skill, key) in skillAverages" 
                :key="key"
                class="skill-bar-container"
              >
                <div class="skill-label">{{ getSkillName(key) }}</div>
                <div class="skill-bar-background">
                  <div 
                    class="skill-bar-fill" 
                    :style="{ width: skill + '%' }"
                    :class="getSkillColorClass(skill)"
                  ></div>
                  <div class="skill-value">{{ skill.toFixed(1) }}%</div>
                </div>
              </div>
            </div>
          </ion-card-content>
        </ion-card>
      </div>

      <!-- Empty State -->
      <div v-else class="empty-state">
        <ion-icon :icon="documentTextOutline" size="large"></ion-icon>
        <h3>Belum ada data raport</h3>
        <p>Silakan ikuti ujian terlebih dahulu</p>
        <ion-button fill="solid" @click="goToExam">Mulai Ujian</ion-button>
      </div>
    </ion-content>
  </ion-page>
</template>

<script setup>
import { 
  IonPage, IonHeader, IonToolbar, IonTitle, IonContent, IonBackButton,
  IonCard, IonCardHeader, IonCardTitle, IonCardSubtitle, IonCardContent,
  IonList, IonItem, IonLabel, IonBadge, IonAvatar, IonSpinner, IonIcon, IonButton
} from '@ionic/vue';
import { documentTextOutline } from 'ionicons/icons';
import { ref, onMounted, computed } from 'vue';
import { useRouter } from 'vue-router';
import { getFirestore, collection, query, where, getDocs, doc, getDoc } from 'firebase/firestore';
import { getAuth } from 'firebase/auth';
import { firebaseApp } from '../firebase.js';

const router = useRouter();
const db = getFirestore(firebaseApp);
const auth = getAuth(firebaseApp);

// Data states
const loading = ref(true);
const examResults = ref([]);
const userPhoto = ref(null);
const userFullName = ref('');

// Computed properties
const averageScore = computed(() => {
  if (examResults.value.length === 0) return 0;
  const sum = examResults.value.reduce((acc, result) => acc + result.score, 0);
  return sum / examResults.value.length;
});

const highestScore = computed(() => {
  if (examResults.value.length === 0) return 0;
  return Math.max(...examResults.value.map(result => result.score));
});

const skillAverages = computed(() => {
  const skills = {
    tps: { total: 0, count: 0 },
    mm: { total: 0, count: 0 },
    lbind: { total: 0, count: 0 },
    lbing: { total: 0, count: 0 }
  };

  examResults.value.forEach(result => {
    if (result.scoreTPS) {
      skills.tps.total += result.scoreTPS;
      skills.tps.count++;
    }
    if (result.scoreMM) {
      skills.mm.total += result.scoreMM;
      skills.mm.count++;
    }
    if (result.scoreLBIND) {
      skills.lbind.total += result.scoreLBIND;
      skills.lbind.count++;
    }
    if (result.scoreLBING) {
      skills.lbing.total += result.scoreLBING;
      skills.lbing.count++;
    }
  });

  return {
    tps: skills.tps.count > 0 ? (skills.tps.total / skills.tps.count) : 0,
    mm: skills.mm.count > 0 ? (skills.mm.total / skills.mm.count) : 0,
    lbind: skills.lbind.count > 0 ? (skills.lbind.total / skills.lbind.count) : 0,
    lbing: skills.lbing.count > 0 ? (skills.lbing.total / skills.lbing.count) : 0
  };
});

// Methods
const fetchExamResults = async () => {
  const user = auth.currentUser;
  if (!user) return;

  try {
    // Get user profile
    const userDoc = await getDoc(doc(db, 'users', user.uid));
    if (userDoc.exists()) {
      userFullName.value = userDoc.data().fullName || 'Pengguna';
      userPhoto.value = userDoc.data().photoURL;
    }

    // Get exam results
    const q = query(
      collection(db, `examResults/${user.uid}/results`),
      where("score", ">", 0)
    );
    const querySnapshot = await getDocs(q);
    
    examResults.value = querySnapshot.docs
      .map(doc => ({ id: doc.id, ...doc.data() }))
      .sort((a, b) => b.createdAt?.seconds - a.createdAt?.seconds);
    
  } catch (error) {
    console.error("Error fetching exam results:", error);
  } finally {
    loading.value = false;
  }
};

const formatDate = (timestamp) => {
  if (!timestamp?.seconds) return 'Tanggal tidak tersedia';
  const date = new Date(timestamp.seconds * 1000);
  return date.toLocaleDateString('id-ID', {
    weekday: 'long',
    year: 'numeric',
    month: 'long',
    day: 'numeric',
    hour: '2-digit',
    minute: '2-digit'
  });
};

const getScoreColor = (score) => {
  if (score >= 80) return 'success';
  if (score >= 60) return 'warning';
  return 'danger';
};

const getSkillName = (key) => {
  const names = {
    tps: 'TPS',
    mm: 'Matematika',
    lbind: 'Literasi B. Indo',
    lbing: 'Literasi B. Inggris'
  };
  return names[key] || key;
};

const getSkillColorClass = (value) => {
  if (value >= 80) return 'skill-high';
  if (value >= 60) return 'skill-medium';
  return 'skill-low';
};

const viewExamDetail = (jadwalId) => {
  router.push(`/exam-detail/${jadwalId}`);
};

const goToExam = () => {
  router.push('/exams');
};

// Lifecycle hooks
onMounted(() => {
  fetchExamResults();
});
</script>

